.header {
  padding-top: 21px;
  padding-bottom: 240.2px; // PP

  color: $white;
  background-color: $promo-back;
  background-image: url("../img/promo-bike.jpg");
  background-position: center -32px;
  background-repeat: no-repeat;
  background-size: cover;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/promo-bike@2x.jpg");
  }

  @media (max-width: $tablet-width-max) {
    padding-top: 0;
    padding-bottom: 0;
    background-size: contain;
    background-position: center -67px;
  }

  @media (max-width: $mobile-width-max) {
    background-size: 172.5% auto; // Pixel Perfect + чтобы картинка тянулась по ширине и совдпадала по макету
    background-position: center 0;
    background-image: url("../img/promo-bike-mobile.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/promo-bike-mobile@2x.jpg");
    }
  }
}

.header__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  max-width: calc(880px + 30px);

  @media (max-width: $tablet-width-max) {
    max-width: none;
  }
}

.header__nav {
  order: -1;
  margin-bottom: 256px;
  grid-column: 1 / 3;

  @media (max-width: $tablet-width-max) {
    display: none;

    grid-row: 1 / 2;
    margin-left: -20px;
    margin-right: -20px;
    margin-bottom: 0;
  }
}

.header--no-js {
  @media (max-width: $tablet-width-max) {
    .header__toggle {
      display: none;
    }

    .header__nav {
      position: static;
      display: flex;
      background-color: $white;
    }
  }
}

.header--opened {
  @media (max-width: $tablet-width-max) {
    .header__toggle {
      z-index: 7;
      background-color: $white;
      background-image: url("../img/icon-cross.svg");
      opacity: 0.2;

      &:hover,
      &:focus {
        opacity: 0.8;
      }

      &:active {
        opacity: 1;
      }
    }

    .header__nav {
      z-index: 2;
      position: fixed;
      display: block;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      overflow: auto;

      margin: 0;

      background-color: $white;
    }
  }
}

.header__wrapper > a {
  @media (max-width: $tablet-width-max) {
    margin-top: 25px;
    order: -3;
  }
}

.header__toggle {
  display: none;
  cursor: pointer;

  @media (max-width: $tablet-width-max) {
    z-index: 1;
    position: absolute;
    top: 0;
    right: 0;

    display: block;
    order: -2;
    width: 50px;
    height: 50px;
    margin-left: auto;
    padding: 0;

    background-image: url("../img/icon-menu.svg");
    font-size: 30px;
    background-position: center;
    background-repeat: no-repeat;
    background-color: $aqua;
    border: none;
  }

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

// Не считаю что это заголовок
.header h2 {
  grid-column: 1 / 2;
  margin-top: 27px;
  margin-bottom: 23px;
  width: 100%;
  font-size: 46px;
  line-height: 64px;
  font-weight: 700;
  letter-spacing: 0.8px;

  @media (max-width: $tablet-width-max) {
    margin-top: 84px;
    margin-bottom: 21px;
    grid-column: 1 / 3;

    font-size: 30px;
    line-height: 42px;
    letter-spacing: 0.6px;
    text-align: center;
  }

  @media (max-width: $mobile-width-max) {
    margin-top: 72px;
    margin-bottom: 20px;
  }
}

.header p {
  grid-column: 1 / 2;
  width: 90%;
  margin-top: 0;
  margin-bottom: 0;
  color: rgba($white, 0.8);
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  letter-spacing: -0.6px;

  @media (max-width: $tablet-width-max) {
    width: 435px;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 80px;
    grid-column: 1 / 3;
    font-size: 18px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -0.5px;
  }

  @media (max-width: $mobile-width-max) {
    margin-bottom: 70px;
  }
}

.header__feedback {
  grid-row: 2 / 5;
  grid-column: 2 / 3;
  margin-left: 30px;

  @media (max-width: $tablet-width-max) {
    order: 1;
    grid-column: 1 / 3;
    grid-row: auto;
  }
}
